
"use client";

import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Textarea } from "@/components/ui/textarea";
import { CheckCircle, ChevronRight, Printer, Send, XCircle } from "lucide-react";
import { useState, useEffect } from "react";

interface OrderLine {
  id: number;
  product: string;
  description: string;
  quantity: number;
  unitPrice: number;
  taxRate: number;
  subtotal: number;
}

export default function RentalOrderPage() {
  const [orderLines, setOrderLines] = useState<OrderLine[]>([
    { id: 1, product: '[FURN_8220] Computer', description: 'Computer', quantity: 1, unitPrice: 1500, taxRate: 15, subtotal: 1500 },
  ]);

  const [untaxedAmount, setUntaxedAmount] = useState(0);
  const [taxAmount, setTaxAmount] = useState(0);
  const [total, setTotal] = useState(0);

  useEffect(() => {
    let newUntaxedAmount = 0;
    let newTaxAmount = 0;

    const updatedOrderLines = orderLines.map(line => {
      const newSubtotal = line.quantity * line.unitPrice;
      newUntaxedAmount += newSubtotal;
      newTaxAmount += newSubtotal * (line.taxRate / 100);
      return { ...line, subtotal: newSubtotal };
    });

    setUntaxedAmount(newUntaxedAmount);
    setTaxAmount(newTaxAmount);
    setTotal(newUntaxedAmount + newTaxAmount);
    
    // Check if there is a difference to avoid infinite loop
    if (JSON.stringify(orderLines) !== JSON.stringify(updatedOrderLines)) {
        setOrderLines(updatedOrderLines);
    }
  }, [orderLines]);

  const handleOrderLineChange = (id: number, field: keyof OrderLine, value: any) => {
    setOrderLines(prevLines =>
      prevLines.map(line =>
        line.id === id ? { ...line, [field]: value } : line
      )
    );
  };
  
  const addProduct = () => {
    const newId = orderLines.length > 0 ? Math.max(...orderLines.map(l => l.id)) + 1 : 1;
    setOrderLines([...orderLines, { id: newId, product: '', description: '', quantity: 1, unitPrice: 0, taxRate: 15, subtotal: 0 }]);
  };


  return (
    <div className="p-4 md:p-8 bg-gray-50">
      <div className="flex flex-col md:flex-row items-start md:items-center justify-between mb-6 gap-4">
        <h1 className="text-xl md:text-2xl font-bold text-gray-800">Rental Orders / S00018</h1>
        <Button>Create</Button>
      </div>

      <div className="flex flex-wrap items-center gap-2 mb-6">
        <Button variant="outline" size="sm" className="flex-grow md:flex-grow-0"><Send className="mr-2 h-4 w-4" /> Send by Email</Button>
        <Button variant="outline" size="sm" className="flex-grow md:flex-grow-0"><Printer className="mr-2 h-4 w-4" /> Print</Button>
        <Button size="sm" className="flex-grow md:flex-grow-0"><CheckCircle className="mr-2 h-4 w-4" /> Confirm Order</Button>
        <Button variant="destructive" size="sm" className="flex-grow md:flex-grow-0"><XCircle className="mr-2 h-4 w-4" /> Cancel</Button>
      </div>

      <div className="flex flex-col md:flex-row items-center justify-between p-4 mb-6 bg-white border rounded-lg gap-4">
        <div className="flex items-center gap-2">
            <div className="flex items-center justify-center w-8 h-8 font-bold text-white bg-primary rounded-full">1</div>
            <span className="font-semibold text-primary">Quotation</span>
        </div>
        <ChevronRight className="text-gray-400 hidden md:block" />
        <div className="flex items-center gap-2 text-gray-500">
           <div className="flex items-center justify-center w-8 h-8 font-bold border-2 border-gray-400 rounded-full">2</div>
           <span>Quotation Sent</span>
        </div>
        <ChevronRight className="text-gray-400 hidden md:block" />
        <div className="flex items-center gap-2 text-gray-500">
            <div className="flex items-center justify-center w-8 h-8 font-bold border-2 border-gray-400 rounded-full">3</div>
            <span>Rental Order</span>
        </div>
      </div>

      <Card>
        <CardContent className="p-4 md:p-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            <div>
              <Label htmlFor="customer">Customer</Label>
              <Input id="customer" defaultValue="Abellio" />
            </div>
            <div>
                {/* Placeholder for alignment */}
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                    <Label htmlFor="invoice-address">Invoice Address</Label>
                    <Textarea id="invoice-address" rows={3} defaultValue="Abellio\n24, New Street, New York, USA" />
                </div>
                 <div>
                    <Label htmlFor="delivery-address">Delivery Address</Label>
                    <Textarea id="delivery-address" rows={3} defaultValue="Abellio\n24, New Street, New York, USA" />
                </div>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                    <Label htmlFor="quotation-date">Quotation Date</Label>
                    <Input id="quotation-date" type="date" defaultValue="2024-03-22" />
                </div>
                 <div>
                    <Label htmlFor="payment-terms">Payment Terms</Label>
                    <Input id="payment-terms" defaultValue="Immediate Payment" />
                </div>
            </div>
          </div>

          <div className="mt-8">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Product</TableHead>
                  <TableHead className="hidden sm:table-cell">Description</TableHead>
                  <TableHead>Quantity</TableHead>
                  <TableHead>Unit Price</TableHead>
                  <TableHead className="hidden sm:table-cell">Taxes (%)</TableHead>
                  <TableHead className="text-right">Subtotal</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {orderLines.map((line) => (
                  <TableRow key={line.id}>
                    <TableCell>
                      <Input
                        value={line.product}
                        onChange={(e) => handleOrderLineChange(line.id, 'product', e.target.value)}
                        className="w-full"
                      />
                    </TableCell>
                    <TableCell className="hidden sm:table-cell">
                      <Input
                          value={line.description}
                          onChange={(e) => handleOrderLineChange(line.id, 'description', e.target.value)}
                          className="w-full"
                        />
                    </TableCell>
                    <TableCell>
                      <Input
                          type="number"
                          value={line.quantity}
                          onChange={(e) => handleOrderLineChange(line.id, 'quantity', parseFloat(e.target.value) || 0)}
                          className="w-20"
                        />
                    </TableCell>
                    <TableCell>
                       <Input
                          type="number"
                          value={line.unitPrice}
                          onChange={(e) => handleOrderLineChange(line.id, 'unitPrice', parseFloat(e.target.value) || 0)}
                          className="w-28"
                        />
                    </TableCell>
                    <TableCell className="hidden sm:table-cell">
                       <Input
                          type="number"
                          value={line.taxRate}
                          onChange={(e) => handleOrderLineChange(line.id, 'taxRate', parseFloat(e.target.value) || 0)}
                          className="w-20"
                        />
                    </TableCell>
                    <TableCell className="text-right">${line.subtotal.toFixed(2)}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
             <Button variant="link" className="mt-2 px-0" onClick={addProduct}>+ Add a product</Button>
          </div>

          <div className="flex justify-end mt-8">
            <div className="w-full md:w-1/2 lg:w-1/3">
              <div className="flex justify-between py-2">
                <span className="text-muted-foreground">Untaxed Amount:</span>
                <span className="font-semibold">${untaxedAmount.toFixed(2)}</span>
              </div>
              <div className="flex justify-between py-2">
                <span className="text-muted-foreground">Taxes:</span>
                <span className="font-semibold">${taxAmount.toFixed(2)}</span>
              </div>
              <div className="flex justify-between py-4 border-t-2">
                <span className="text-lg font-bold">Total:</span>
                <span className="text-lg font-bold">${total.toFixed(2)}</span>
              </div>
            </div>
          </div>
          
           <div className="mt-8">
            <Label htmlFor="terms">Terms and Conditions</Label>
            <Textarea id="terms" rows={3} />
          </div>

        </CardContent>
      </Card>
    </div>
  );
}
